@import '../node_modules/ngx-toastr/toastr.css';

/* You can add global styles to this file, and also import other style files */
html,
body {
    height: 100%;
}

* {
    margin: 0;
    font-family: Coda;
    font-weight: 400;
    line-height: 28.4px;
    box-sizing: border-box;
}

.errors {
    color: red;
    font-size: 0.65rem;
}

.toast-container {
    top: 50px !important;
    font-family: Coda;
    font-weight: 400;
    line-height: 28.4px;
    box-sizing: border-box;
}

.toast-success {
    color: white;
    background-color: #78a1e3;
}